import './index.css';
import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Route, Redirect, Switch } from 'react-router-dom';
import App from './App';
import Home from './Home';
import Competitions from './Competitions';
import Register from './screen/Register'
import Activate from './screen/Activate'
import Login from './screen/Login'
import ForgetPassword from './screen/ForgetPassword';
import ResetPassword from './screen/ResetPassword';
import PrivateContent from './screen/Private';

import FormProfil from './screen/FormProfile'
import PrivateRoute from './Routes/PrivateRoute';
import AdminRoute from './Routes/AdminRoute';
import Lomba from './screen/Lomba';
import NotYetRegistered from './screen/NotYetRegistered';
import Dashboards from './screen/Dashboard';
import DashboardLomba from './screen/Poster'
import Elektro from './screen/Elektro'
import Paper from './screen/Paper'

import TestUpload from './screen/TestUpload'

import LombaPoster from './screen/LombaPoster';
import LombaPaper from './screen/LombaPaper';
import LombaElectrical from './screen/LombaElectrical';
import LombaAdmin from './screen/LombaAdmin'
import ProfileFix from './screen/ProfileFix'

import SubmisiElektro from './screen/UploadSubmisiElektro'
import SubmisiPaper from './screen/UploadSubmisiPaper'
import SubmisiPoster from './screen/UploadSubmisiPoster'

import BuktiPembayaranElektro from './screen/UploadBuktiPembayaranElektro'
import BuktiPembayaranPaper from './screen/UploadBuktiPembayaranPaper'
import BuktiPembayaranPoster from './screen/UploadBuktiPembayaranPoster'




import 'react-toastify/dist/ReactToastify.css';
import DashboardTemplate from './screen/DashboardTemplate';



ReactDOM.render(
  <BrowserRouter>
    <Switch>
      <Route path='/debug' exact render={props => <DashboardTemplate content={<App />} {...props} />} />
      <Route path='/' exact render={props => <Home {...props} />} />
      <Route path='/competitions' exact render={props => <Competitions {...props} />} />
      <Route path='/register' exact render={props => <Register {...props} />} />
      <Route path='/users/password/reset/:token' exact render={props => <ResetPassword {...props} />} />
      <Route path='/users/activate/:token' exact render={props => <Activate {...props} />} />
      <Route path='/login' exact render={props => <Login {...props} />} />


      <Route path='/users/password/forget' exact render={props => <ForgetPassword {...props} />} />

      <PrivateRoute path="/profil" exact component={DashboardTemplate} content={<PrivateContent />} />
      <PrivateRoute path="/lomba" exact component={DashboardTemplate} content={<Lomba />} />
      <PrivateRoute path="/dashboard" exact component={DashboardTemplate} content={<PrivateContent />} />
      <PrivateRoute path="/notyetregistered/:lomba" exact component={DashboardTemplate} content={<NotYetRegistered />} />

      <PrivateRoute path="/poster" exact component={DashboardTemplate} content={<DashboardLomba />} />
      <PrivateRoute path="/elektro" exact component={DashboardTemplate} content={<Elektro />} />
      <PrivateRoute path="/paper" exact component={DashboardTemplate} content={<Paper />} />

      <PrivateRoute path="/testupload" exact component={DashboardTemplate} content={<TestUpload />} />



      <PrivateRoute path="/editprofil" exact component={DashboardTemplate} content={<FormProfil />} />


      <AdminRoute path="/admin/poster" exact component={LombaPoster} />
      <AdminRoute path="/admin/paper" exact component={LombaPaper} />
      < AdminRoute path="/admin/elektro" exact component={LombaElectrical} />
      < AdminRoute path="/admins" exact component={LombaAdmin} />

      <PrivateRoute path="/submisielektro" exact component={DashboardTemplate} content={<SubmisiElektro />} />
      <PrivateRoute path="/submisipaper" exact component={DashboardTemplate} content={<SubmisiPaper />} />
      <PrivateRoute path="/submisiposter" exact component={DashboardTemplate} content={<SubmisiPoster />} />

      <PrivateRoute path="/buktielektro" exact component={DashboardTemplate} content={<BuktiPembayaranElektro />} />
      <PrivateRoute path="/buktipaper" exact component={DashboardTemplate} content={<BuktiPembayaranPaper />} />
      <PrivateRoute path="/buktiposter" exact component={DashboardTemplate} content={<BuktiPembayaranPoster />} />


      <PrivateRoute path="/datadiri" exact component={DashboardTemplate} content={<ProfileFix />} />



    </Switch>
  </BrowserRouter>,
  document.getElementById('root')
);
