import { Link } from 'react-router-dom';
import Logo from '../assets/LogoNescoRounded.png';
import React, { useRef, useEffect, useLayoutEffect, useCallback } from 'react';
import { authenticate, isAuth, isAdmin, LocalUserData } from '../helpers/auth';

export default function Navbar() {

  const [isSticky, setIsSticky] = React.useState(true);
  const [isOpened, setIsOpened] = React.useState(false);

  const onScroll = () => {
    if (window.scrollY > 30) {
      setIsSticky(true);
    } else {
      setIsSticky(false);
    }
  }


  useEffect(() => {
    window.addEventListener('scroll', onScroll);

    return () => {
      window.removeEventListener('scroll', onScroll);
    }
  });


  return (
    <div className={`fixed top-0  inline-block  w-full items-stretch`}>
      <nav className={` top-0 w-full ju transition z-10 text-white  ${isSticky ? "bg-gradient-to-r from-nesco-bg-purple to-nesco-bg-blue shadow-2xl " : " absolute top-0 bg-transparent"} `}>
        <div className=" mx-auto w-full px-6 py-2 flex justify-between items-center">
          <div className=" inline-flex align-middle h-16 items-center">
            <Link to="/">
              <img alt='' src={Logo} className=' h-12'></img></Link>
            <Link to="/"><h1 className=" m-4 text-base lg:text-4xl  font-extrabold text-white hover:text-blue-200">NESCO 2022</h1></Link>


          </div>
          <ul className=' hidden md:inline-flex justify-around list-none'>
            <li>
              <Link to="/" className='px-4 hover:text-blue-200'>FAQ</Link>
              <Link to="/competitions" className='px-4 hover:text-blue-200'>Competitions</Link>
              <Link to="/" className='px-4 hover:text-blue-200'>Webinar</Link>
              <Link to="/" className='px-4 hover:text-blue-200'>Gallery</Link>
              <Link to="/login" className='px-4 hover:text-blue-200'>Sign In</Link>
              {isAuth() ?
                <Link to="/debug" className='px-4 hover:text-blue-200'>Debug</Link> : null}
            </li>
          </ul>
          <div className=' md:hidden' onClick={() => setIsOpened(!isOpened)}>
            <svg
              className="fill-current h-3 w-3"
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <title>Menu</title>
              <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" width={3} />
            </svg>
          </div>

        </div>
      </nav>
      {isOpened && (<div className=' md:hidden '>
        <ul className='rounded-lg transition-all bg-nesco-bg-blue shadow-md fixed right-0 top-20 list-none text-white z-10'>
          <li className=' p-4 px-8'><Link to="/" className='hover:text-blue-200'>FAQ</Link></li>
          <li className=' p-4 px-8'><Link to="/" className='hover:text-blue-200'>Competitions</Link></li>
          <li className=' p-4 px-8'><Link to="/" className='hover:text-blue-200'>Webinar</Link></li>
          <li className=' p-4 px-8'><Link to="/" className='hover:text-blue-200'>Gallery</Link></li>
          <li className=' p-4 px-8'><Link to="/login" className='hover:text-blue-200'>Sign In</Link></li>
          <li className=' p-4 px-8'><Link to="/debug" className='hover:text-blue-200'>Debug</Link></li>
        </ul>
      </div>)}
    </div>
  );
}